.result-wrap{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    max-width: 600px;
    padding: 10px;
}

.logo{
    width: 150px;
    display: block;
    margin: 1em auto;
}

button{
    display: block;
    border: none;
    background-color: transparent;
    font-size: 1em;
    padding: .3em 1em;
    border-radius: 20px;
    width: 100%;
    margin-bottom: .8em;
}

.btn1{
    background-color: #0070ba;
    color: #fff;
}

.btn2{
    color: #005a95;
    font-weight: bold;
}

h1{
    font-size: 1em;
    margin-bottom: 5em;
    font-weight: normal;
}


@media screen and (max-width: 1024px) {
    .result-wrap{
        width: 100%;
    }
    .logo{
        width: 80px;
    }
    h1{
        text-align: center;
        font-size: .8em;
    }
}